import React from "react";
import "./DateRangeSelector.css";

// Defining the available date ranges with their labels and values
const dateRanges = [
  { label: "7 days", value: "7d" },
  { label: "30 days", value: "30d" },
  { label: "3 months", value: "3m" },
  { label: "12 months", value: "12m" },
  { label: "Year to Date (YTD)", value: "ytd" },
  { label: "Max", value: "max" }, // Newly added range
];

// Props typing for this component
interface DateRangeSelectorProps {
  selectedRange: string; // Currently selected time range
  onRangeChange: (range: string) => void; // Function to be called when range changes
}

// Functional component that receives the selected range and the change function as props
const DateRangeSelector: React.FC<DateRangeSelectorProps> = ({
  selectedRange,
  onRangeChange,
}) => {
  return (
    <div className="date-range-selector">
      {/* Container for the range buttons */}
      <div className="date-range-options">
        {/* Mapping defined ranges to generate a button for each */}
        {dateRanges.map((range) => (
          <button
            key={range.value}
            className={`range-button ${
              selectedRange === range.value ? "active" : ""
            }`}
            onClick={() => onRangeChange(range.value)}
          >
            {range.label}
          </button>
        ))}
      </div>
    </div>
  );
};

export default DateRangeSelector;
